.ep_main_frame {
    display: grid;
    grid-template-columns: minmax(150px, 10%) 1fr;
}

.ep_header_bar {
    display: flex;
    flex-direction: row;
    margin-left: 20px;
}

.ep_header_bar > div {
    margin: 10px;
}


.left_bar {
    display: flex;
    flex-direction: column;
}

.left_bar > div {
    margin: 10px;
}

.ep_right_content{
    display: flex;
    flex-direction: row;
}
.ep_edit_panel{
    border-radius: 5px;
    border: 1px solid gray;
    box-shadow: 2px 2px darkgrey;
    padding: 20px 40px 20px 40px;

}
.ep_form {
    display: flex;
    flex-direction: column;
    padding: 10px;
}

.ep_form > * {
    margin: 10px;
}

.methods {
    display: flex;
    flex-direction: row;
}

.ep_save_iframe {
    border: none;
    height: auto;
}