.item {
    text-align: left;
    border-radius: 5px;
    border-width: 1px;
    border-color: black;
    min-height: 100px;
    padding: 20px;
}

.fn_main_frame {
    display: grid;
    grid-template-columns: minmax(150px, 20%) 1fr;
    height: 100%;
}

.fn_left_side {
    display: flex;
    flex-direction: column;
    box-shadow: black 0 5px 5px;
}

.fn_list_title {
    padding: 10px;
}

.fn_list {
    display: flex;
    flex-direction: column;
}

.fn_list > div {
    padding: 10px;
}

.fn_list > div:hover {
    background-color: gray;
}

.fn_right_side {
    display: flex;
    flex-direction: row;
}

.fn_detail_content {
    padding: 20px;
}

.fn_detail_content > * {
    margin: 10px;
    width: auto;
}
.detail_code{
    border: black 1px solid;
    border-radius: 5px;
    padding: 20px;
}

.fn_edit_content {
    display: flex;
    flex-direction: column;
}

.fn_content {
    display: flex;
    flex-direction: column;
    border-style: solid;
    border-width: thin;
    border-color: #dadce0;
    border-radius: 8px;
    padding: 40px 20px;
    margin: 10px;
}

.fn_content > * {
    margin: 10px;
    width: auto;
}


.fn_save_iframe {
    border: none;
    height: 50px;
}

.fn_editor {
    min-height: 200px;
}

.fn_button {
    border-width: 1px;
    border-radius: 5px;
    box-shadow: 0 2px 5px gray;
}